	module.exports = {"data":{"post":{"html":"<p><a href=\"http://store.playmobilusa.com/is-bin/INTERSHOP.enfinity/eCS/Store/en/-/USD/PM_DisplayProductInformation-Start;sid=fczBM3YGcUPBIjfLe63NFbg0OnNIO01W6-U=?ProductSKU=3185&CategoryName=US_storefront&PLS=0#\"><img alt=\"playmo_plane.jpg\" src=\"http://www.papascott.de/archives/fotos/playmo_plane.jpg\" width=\"150\" height=\"118\" border=\"0\" align=\"right\" /></a> We could handle it when Christopher got us up at 6:30 on Christmas Eve morning. We can understand the anticipation on the day when Santa arrives (at our house, he arrives while we are at church and leaves his package on Christopher's sled on the patio) and the presents will be opened. We can sleep late the next day.</p>\n<p>Wrong. This morning Christopher was up at 5:30 to play with his new toys. The first thing he did was kiss each one, so I guess he likes them. The highlight is the <a href=\"http://http://store.playmobilusa.com/is-bin/INTERSHOP.enfinity/eCS/Store/en/-/USD/PM_DisplayProductInformation-Start;sid=fczBM3YGcUPBIjfLe63NFbg0OnNIO01W6-U=?ProductSKU=3185&CategoryName=US_storefront&PLS=0#\">Playmobil Airplane</a>, which even the adults think is pretty cool.</p>","fields":{"title":"Earlier and Earlier","date":"25 Dec 2004","url":"https://papascott.de/2004-12-25-earlier-and-earlier/"}}},"pathContext":{"slug":"/2004-12-25-earlier-and-earlier/","prev":{"fields":{"title":"Kills Every Taste Bud","date":"23 Dec 2004","slug":"/2004-12-23-toetet-jede-geschmacksknospe/"}},"next":{"fields":{"title":"Tidal Waves Then and Now","date":"26 Dec 2004","slug":"/2004-12-26-tidal-waves-then-and-now/"}}}}

